import React, { useState, useEffect } from 'react';
import { Link } from "react-router-dom";

import "../css/header.css";

const spacing = {
	margin: '0 0.3rem',
};

const style_resume = {
	textDecoration: "none",
	color: "white",
	fontSize: "20px",
	marginLeft: "1rem",
	marginRight: "1rem",
};

const LinkStyle = {
	backgroundColor: "rgba(0,0,0,0)",
	color: "white",
	fontSize: "20px",
	margin: "0",
	padding: "0.4rem",
	textDecoration: "none",
};

const HeaderLeft = () => (
	<div className="flex_group">
		<div>
		<a className="logo-link" href="/">Jiayi Zhao</a>
		</div>
		<div style={spacing}/>
		<NonHamburgerMenu/>
	</div>
);

const NonHamburgerMenu = () => (
	<div className="non-hamburger-menu">
	<Link className="header-link" to="/">Home</Link>
	<Link className="header-link" to="/experiences">Experiences</Link>
	<Link className="header-link" to="/projects">Projects</Link>
	</div>
);

function HamburgerMenu(props) {
	const [ visible, setVisible ] = useState(false);

	async function handleVisible(event) {
		event.preventDefault();
		setVisible(!visible);
		console.log(visible);
	}

	if (!visible) {
		return (
		<div className="hamburger-menu" onClick={e => handleVisible(e)}>
			<div className="bar1"></div>
			<div className="bar2"></div>
			<div className="bar3"></div>
		</div>
		);
	}

	return (
	<div className="hamburger-menu">
	<div className="hamburger-button change" onClick={e => handleVisible(e)}>
		<div className="bar1"></div>
		<div className="bar2"></div>
		<div className="bar3"></div>
	</div>
	</div>
	);
}

function Header(props) {
	const renderDefaultHeader = (
		<nav className="topnav">
		<HeaderLeft/>
		<div className="flex_group">
			<HamburgerMenu/>
		</div>
		</nav>
	);
	return renderDefaultHeader;
}

export default Header;
export { Header };
